import FeatureService from './feature.service'

export default class FeatureComponent {
    constructor(featureId) {
        this.feature = FeatureService.getFeatureById(featureId)
    }

    createElement() {
        const template = document.getElementById('feature-template')
        const instance = template.content.cloneNode(true)

        const { id, name, description, image } = this.feature

        instance.querySelector('.feature-name').textContent = name
        instance.querySelector('.feature-name').id = id
        instance.querySelector('.feature-description').textContent = description

        const logoElement = instance.querySelector('img.feature-image')
        logoElement.src = `public/images/features/${image}`
        logoElement.alt = name
        return instance
    }
}