import ProviderService from './provider.service.js'
import { clearChildren, createLinkElement } from '../common'

const newListItem = (content, icon) => {
    const li = document.createElement("li");
    const i = document.createElement("i")
    i.className = icon
    li.appendChild(i);
    li.appendChild(document.createTextNode(content))
    return li;
}

const newDataTypeItem = (dataType, timestep) => {
    const li = document.createElement("li")
    const dataTypeSpan = document.createElement("span")
    dataTypeSpan.className = "data-type"
    dataTypeSpan.appendChild(document.createTextNode(dataType))
    const timeStepSpan = document.createElement("span")
    timeStepSpan.className = "time-step"
    timeStepSpan.appendChild(document.createTextNode(timestep))
    li.appendChild(dataTypeSpan);
    li.appendChild(document.createTextNode(' au pas de temps '))
    li.appendChild(timeStepSpan);
    return li;
}

const newPurposeItem = (id, name) => {
    const li = document.createElement("li")
    
    const link = createLinkElement({
        content: name,
        href: `#${id}`
    })
    li.appendChild(link);
    return li;
}

export default class ProviderComponent {
    constructor(providerName) {
        this.provider = ProviderService.getProviderByName(providerName)
    }

    createElement() {
        const template = document.getElementById('provider-template')
        const instance = template.content.cloneNode(true)

        const { name, image, description, link, devices, prerequisites, datatypes, purposes } = this.provider

        instance.querySelector('.provider-name').textContent = name
        instance.querySelector('.provider-name').id = name
        instance.querySelector('.provider-description').textContent = description
        instance.querySelector('.provider-link').href = link

        const logoElement = instance.querySelector('img.provider-image')
        logoElement.src = `public/images/partners/${image}`
        logoElement.alt = name

        if (devices && devices.length > 0) {
            const list = instance.querySelector('ul.device-list')
            clearChildren(list)
            devices.map(({name, icon}) => newListItem(name, icon)).forEach(x => list.appendChild(x));
        }

        if (prerequisites && prerequisites.length > 0) {
            const list = instance.querySelector('ul.prerequisite-list')
            clearChildren(list)
            prerequisites.map(({name, icon}) => newListItem(name, icon)).forEach(x => list.appendChild(x));
        }

        if (datatypes && datatypes.length > 0) {
            const list = instance.querySelector('ul.collected-data')
            datatypes.map(({name, timestep}) => newDataTypeItem(name, timestep)).forEach(x => list.appendChild(x));
        }

        if (purposes && purposes.length > 0) {
            const list = instance.querySelector('ul.purpose-list')
            purposes.map(({id, name}) => newPurposeItem(id, name)).forEach(x => list.appendChild(x));
        }

        return instance
    }
}