import ProviderService from './provider/provider.service'
import ProviderComponent from './provider/provider.component.js'

import FeatureService from './feature/feature.service'
import FeatureComponent from './feature/feature.component.js'

import { clearChildren, createLinkElement } from './common'

const newMenuItem = (link) => {
    const menuItem = document.createElement('li');
    menuItem.appendChild(link)
    return menuItem;
}

const partnerList = document.getElementById("partners")
clearChildren(partnerList)
for (let name of ProviderService.getProviderNames()) {
    const link = createLinkElement({
        content: name, 
        href: `#${name}`,
        className: "menu-item"
    });
    partnerList.appendChild(newMenuItem(link));
    document.querySelector('main').appendChild(new ProviderComponent(name).createElement())
}

const featureList = document.getElementById("features")
clearChildren(featureList)
for (let feature of FeatureService.getFeatureIdNames()) {
    const link = createLinkElement({
        content: feature.name, 
        href: `#${feature.id}`,
        className: "menu-item"
    });
    featureList.appendChild(newMenuItem(link));
    document.querySelector('main').appendChild(new FeatureComponent(feature.id).createElement())
}